/* create styles to be minimal for import building  */
/* App defaulted to black and white read text*/
/* Containers to be three defaulted to black and white read text*/
.App {
  text-align: center;

}
img{
width:100%;
 max-width:300px;
 height:200px; 

}
.active{
color:yellow;
}
Link {
  width:25%;
  height:800px;
  color:black;

}
.Home, html{
  background: grey;

}
h1{
  color:black;
}
.Container {
  background-color: #ccc;
  padding: 100px;
  padding-left: 25%;
  width:100%;
  height:2000px;
  -webkit-box-shadow: 0 3px 4px #ccc;
          box-shadow: 0 3px 4px #ccc;
}
.Gallery, .Art {
  color:#000;
  padding: 100px;
  padding-left: 25%;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.Navigation {
  background: #ccc !important;
  height: 200px;
  width:100%;
  padding: 20px;
  padding: 100px;
  padding-left: 25%;
  color: black !important;
}

.App-title {
  font-size: 1.5em;
}

.App-logo{
  font-size: large;
}
.App-intro{
  background:#ccc;
  padding-left:25%;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



